// body { overflow-hidden;}
.loader {
  position: fixed;
  display: flex;
  height: 100vh;
  width: 100%;
  justify-content: center;
  align-items: center;
  z-index: 1231;
  background: #512DA8;
  }


  .one {
    position: absolute;
    border-radius: 50%;
    background: #FFFFFF;
    opacity: .0;
    animation: loading 1.3s .65s infinite;
    height: 20px;
    width: 20px;
  }

  .two {
    position: absolute;
    border-radius: 50%;
    background: #FF4081;
    opacity: .0;
    animation: loading 1.3s infinite;
    height: 20px;
    width: 20px;
  }


  @keyframes loading {
    0% {
      opacity: .0;
      transform: scale(.15);
      box-shadow: 0 0 2px rgba(black, .1);
    }
    50% {
      opacity: 1;
      transform: scale(2);
      box-shadow: 0 0 10px rgba(black, .1);
    }
    100% {
      opacity: .0;
      transform: scale(.15);
      box-shadow: 0 0 2px rgba(black, .1);
    }
  }
